import func from './vue-temp/vue-editor-bridge';
<template>
  <div class="mainCon selefService">
    <div class="mainTop">
      <div class="mainRight">
        <!-- <div style='height:1.5vh;width: 100%; background: #f1f1f1;'></div> -->
        <!-- <div class="orderList">待提交订单</div> -->
         <!-- style="padding: 5px 0 5px 20px; border-top-left-radius: 21px;border-top-right-radius: 5px;" -->
        <div class="orderList">
        <el-button type="danger" size="mini" @click="clickDelCart"><i class="el-icon-close "></i>删除</el-button>
        <el-button
          type="success"
          icon="el-icon-refresh"
          size="mini"
          @click="handleClickRefresh"
        >刷新</el-button> 
      </div>
      <div style="position:absolute; bottom: -5px!important;right: 30px;">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="pageSize"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total">
        </el-pagination>
        <div class="common" >共{{ total }}笔</div>
        </div>
      </div>
    </div>
    <div class="mainBottom">
      <!-- margin-right:10px;margin-top: 10px;margin-bottom: 10px; -->
      <el-table
        :data="tableData"
        style="width: 100%"
        stripe
        height="47vh"
        v-loading="loading"
        @selection-change="handleSelectionChange">
        <el-table-column
          fixed
          type="index"
          align="center"
          min-width="50">
        </el-table-column>
        <el-table-column
          fixed
          type="selection"
          align="center"
          width="50">
        </el-table-column>
        <el-table-column
          prop="date"
          label="图片"
          align="center"
          min-width="50">
          <template slot-scope="scope">
            <span v-if="scope.row">
              <el-tooltip placement="right-start">
                <div slot="content">
                  <img v-lazy="bigImgSrc" alt srcset class="bigImgCon">
                </div>
                <img v-if="scope.row.goods_imgs.length!==0" v-lazy="scope.row.goods_imgs[0].path" alt srcset ref="user_image" @mouseover="mouseover(scope.row.goods_imgs)" @mousewheel="wheel(scope.row.goods_imgs)">
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="goods_name"
          show-overflow-tooltip
          align="center"
          label="商品标题">
        </el-table-column>
        <el-table-column
          prop="goods_weight"
          label="重量（KG）"
          show-overflow-tooltip
          align="center"
          min-width="90">
        </el-table-column>
        <el-table-column
          prop="goods_price"
          label="单价"
          align="center"
          show-overflow-tooltip
          min-width="55">
        </el-table-column>
        <el-table-column
          prop="productNumber"
          label="数量"
          align="center"
          min-width="150">
          <template slot-scope="scope">
            <el-input-number size="small" :min="0" :max="999999999" :value="scope.row.goods_num" v-model="scope.row.goods_num"  @change="handleChange(scope.row.goods_num,scope.row.id)"></el-input-number>
          </template>
        </el-table-column>
      </el-table>
      <div style='height:1.5vh;width: 100%; background: #f1f1f1;'></div>
      <div class="mainRightBottom">
        <!-- <div style="height: 100%;margin: 10px;margin-top:30px;border-radius: 10px;box-shadow: 5px 5px 20px #ddd;"> -->
        <el-form :model="sizeForm" :rules="rules" ref="ruleForm" label-width="80px" size="mini" :inline-message="true">
          <el-form-item label-width="10px" size="large" class="marginBottm5 lineHeight20 textLeft">
            (商品合计：<span class="colorRed">{{ parseInt(price).toFixed(2) }}</span> + 快递费用：<span class="colorRed">{{ parseInt(priceTwo).toFixed(2)}}</span> = 订单总额：<span class="colorRed">{{ parseInt(price+priceTwo).toFixed(2) }}</span>)
          </el-form-item>
          <el-form-item label="送达地区" class="width37 disInlineBlock marginBottm10" required prop="country">
            <el-select v-model="sizeForm.country" placeholder="送达国家" @change="countryChange(sizeForm.country)">
              <el-option v-for="(item,index) in countryList" :key="index" :label="item.zone_name" :value="item.zone_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=""  label-width="0px" class="width20 disInlineBlock marginBottm10 onlyFormItem" required prop="province">
            <el-select
              v-model="sizeForm.province"
              filterable
              allow-create
              default-first-option
              placeholder="请选择省份">
              <!-- <el-option v-for="(item,index) in cityList" :key="index" :label="item.zone_name" :value="item.zone_id"></el-option> -->
              <el-option v-for="(item,index) in cityList" :key="index" :label="item.zone_name" :value="item.zone_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="城市" class="width40 disInlineBlock marginBottm10" prop="city">
            <el-input v-model="sizeForm.city" placeholder="请输入城市名称"></el-input>
          </el-form-item>
          <el-form-item label="详细地址" class="width58 disInlineBlock marginBottm10" prop="address">
            <el-input v-model="sizeForm.address" placeholder="请输入详细地址"></el-input>
          </el-form-item>
          <el-form-item label="邮编" class="width40 disInlineBlock marginBottm10" prop="zipcode">
            <el-input v-model="sizeForm.zipcode" placeholder="请输入邮编"></el-input>
          </el-form-item>
          <el-form-item label="收件人" class="width58 disInlineBlock marginBottm10" prop="consignee">
            <el-input v-model="sizeForm.consignee" placeholder="请输入收件人"></el-input>
          </el-form-item>
          <el-form-item label="电话" class="width40 disInlineBlock marginBottm10">
            <el-input v-model="sizeForm.mobile" placeholder="请输入联系电话"></el-input>
          </el-form-item>
          <el-form-item label="订单号" class="width58 disInlineBlock marginBottm10" prop="order_sn">
            <el-input v-model="sizeForm.order_sn" :value="sizeForm.order_sn" @change="orderChange" placeholder="为防止重复下单，请输入（amazon,wish）的订单号"></el-input>
          </el-form-item>
          <el-form-item label="物流" class="width40 disInlineBlock marginBottm10" prop="shipping_code">
            <el-select v-model="sizeForm.shipping_code" placeholder="请选择物流公司" @change="logisticsChange">
              <el-option v-for="(item,index) in logisticsList" :key="index" :label="item.logistics_name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单备注" class="marginBottm10">
            <el-input type="textarea" v-model="sizeForm.user_note" placeholder="如果有特殊情况，请详细备注！"></el-input>
          </el-form-item>
          <el-form-item size="large" class="marginBottm5 lineHeight20 textCenter marginRight10">
            <el-button type="primary" size="mini"><i class="iconfont icon_chakan"></i>查看生产图</el-button>
            <el-button type="success" size="mini" @click="submitForm('ruleForm')"><i class=" el-icon-check"></i>提交订单</el-button>
          </el-form-item>
        </el-form>
      <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>
  let imgIndex = 0;//bigImg索引
export default {
  data(){
    let validatorOrder = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请填写订单号'));
        } else {
          callback();
        }
      };
    return {
      loading: false,
      currentPage: 1,//第几页
      pagerCount: 5,//按钮数量
      pageSize: 50,
      total: 0,
      shopNumber: 10,//商品数量
      //初始化商品图片
      bigImgSrc: '',
      // tableColumnState: false,//表格展开行显示状态
      tableData: [],
      multipleSelection: [],
      activeName: 'first',//标签页默认显示
      //国家列表
      countryList: [],
      //城市列表
      cityList: [],
      //物流列表
      logisticsList: [],
      sizeForm: {
        address: '',
        city: '',
        country: '',
        consignee: '',
        // goods_num: 0,
        goods_id: [],
        mobile: '',
        order_sn: '',
        province: '',
        shipping_price: 0.00,
        total_amount: '',
        zipcode: '',
        user_note: '',
        shipping_code: ''
      },
        price: 0,
        priceTwo: 0,
        rules: {
          country: [
            { required: true, message: '请选择国家', trigger: 'change' }
          ],
          province: [
            { required: true, message: '请选择城市', trigger: 'change' }
          ],
          city: [
            { required: true, message: '请填写城市', trigger: 'blur' }
          ],
          address: [
            { required: true, message: '请填写详细地址', trigger: 'blur' }
          ],
          consignee: [
            { required: true, message: '请填写收件人', trigger: 'blur' }
          ],
          order_sn: [
            { required: true, validator: validatorOrder, trigger: 'blur' }
          ],
          shipping_code: [
            { required: true, message: '请选择物流公司', trigger: 'change' }
          ],
        }
    }
  },
   methods: {
    handleSizeChange(val) {
      this.currentPage = val;
      this.initData(this.pageSize,this.currentPage);
    },
    handleCurrentChange(val) {
      this.pageSize = val;
      this.initData(this.pageSize,this.currentPage);
    },
    //商品数量改变
    handleChange(val,id){
      this.addShop(val,id);
    },
    //国家信息发生改变
    countryChange(val){
      this.getCity(val);
    },
    //物流信息发生改变
    async logisticsChange(val){
      let logisticsPrice =this.logisticsList.filter(item => item.id==val );
      this.postRequest(`/v1/goodsPostagetotal?courier_id=${logisticsPrice[0].id}&country_id=${logisticsPrice[0].zone_id}`)
      .then(res => {
        if(res.data.code=='200'){
          this.priceTwo = res.data.data;
          this.sizeForm.shipping_price = res.data.data;
        }
      })
      .catch(error => {
      })
    },
    //物流过滤器（获取该物流信息对应国家的物流费用）
    // logisticsPriceFilter(logistics,val){
      // let logisticsPrice = logistics.filter(item => item.id==val );
      // this.sizeForm.shipping_price = logisticsPrice[0].price;
      // this.sizeForm.shipping_code = val;
      // this.sizeForm.total_amount = Number(this.price) + Number(this.sizeForm.shipping_price);
      // ;
      // ;
    // },
    //对比订单号
    async orderChange(val){
      if(val!=''){
        let res = await this.api.goodsSn({order_sn: val})
        // this.postRequest(`/v1/goodsSn?order_sn=${val}`)
        // .then((res)=>{
        if(res.data.data=='0'){
          this.$message.error('订单号已存在，请重新填写');
          this.sizeForm.order_sn = '';
          this.$refs.ruleForm.validateField('order_sn');
        }else {
          return false;
        }
      }else {
        return false;
      }
    },
    async addShop(val,id){
      let res = await this.api.goodsNumber({
        cart_id: id,
        goods_num: val
      })
      if(res.data.data.status=='1'){
        this.price = res.data.data.sum;
      }
      if(res.data.data.status=='3'){
        this.initData(this.pageSize,this.currentPage);
      }
      if(res.data.data.status=='4'){
        this.initData(this.pageSize,this.currentPage);
        this.price = 0;
      }
    },
    //删除购物车商品
    async clickDelCart(){
      let ids = [];
      if(this.multipleSelection.length>0){
        for (const key in this.multipleSelection) {
          if (this.multipleSelection.hasOwnProperty(key)) {
            const element = this.multipleSelection[key];
            ids.push(element.id);
          }
        }
        let res = await this.api.CartDelete({id: `${ids.toString()}`})
        if(res.data.code=='200'){
          this.handleClickRefresh();
          this.$message({
            type: 'success',
            message: '删除成功'
          })
        }else {
          this.errorMess('删除失败')
        }
      }
    },
    //刷新页面
    handleClickRefresh(){
      this.loading = true;
      this.initData(this.pageSize,this.currentPage);
      this.initCountryCity();
      this.loading = false;
    },
    //提交订单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postOrder();
        } else {
          return false;
        }
      });
     },
    //提交订单->核验完成后
    async postOrder(u_id=sessionStorage.getItem('selectUId')){
      if(this.multipleSelection.length>0){
        this.sizeForm.goods_id = [];
        for (const key in this.multipleSelection) {
          if (this.multipleSelection.hasOwnProperty(key)) {
            const element = this.multipleSelection[key];
            this.sizeForm.goods_id.push(element.goods_id);
          }
        }
        this.sizeForm.goods_id = this.sizeForm.goods_id.join(',');
        let res = await this.api.goodsAddOrder(this.sizeForm)
        // this.postRequest(`/v1/goodsAddOrder?u_id=${u_id}`,this.sizeForm)
          // .then((res)=>{
        if(res.data.data=='1'){
          this.$message({
            message: '下单成功',
            type: 'success'
          });
          this.initData(this.pageSize,this.currentPage);
          this.sizeForm = {
            address: '',
            city: '',
            country: '',
            consignee: '',
            // goods_num: 0,
            goods_id: [],
            mobile: '',
            order_sn: '',
            province: '',
            shipping_price: 0.00,
            total_amount: '',
            zipcode: '',
            user_note: '',
            shipping_code: ''
          }
          this.multipleSelection = [];
        }else {
          this.$message.error('下单失败');
        }
      }
    },
    //初始化待提交订单列表数据
    async initData(pageSize,currentPage,u_id=sessionStorage.getItem('selectUId')){
      this.loading = true;
      let res = await this.api.cartLists({
        limit: pageSize,
        page: currentPage
      })
      // this.postRequest(`/v1/cartLists?limit=${pageSize}&page=${currentPage}&u_id=${u_id}`)
      // .then((res)=>{
      if(res.data.code == 200) {
        this.tableData = res.data.data.goods_orders.data;
        this.total = res.data.data.goods_orders.total;
        this.bigImgSrc = res.data.data.goods_orders.data[0] ? res.data.data.goods_orders.data[0].goods_imgs[0].path : '';
        this.multipleSelection = []
        for (let index = 0; index < res.data.data.goods_orders.data.length; index++) {
          const element = res.data.data.goods_orders.data[index];
          this.multipleSelection.push(element);
        }
        this.sizeForm.goods_num = res.data.data.count;
        this.price = res.data.data.sum;
        this.loading = false;
      }
    },
    //初始化国家地区列表
    async initCountryCity(){
      let res = await this.api.goodsCoutry()
      if(res.data.code == 200) {
        this.countryList = res.data.data;
      }   
    },
    //获得该国家的城市
    async getCity(cityId){
      let res = await this.api.goodsCity({country_id: cityId})
      if(res.data.code == 200) {
        this.cityList = res.data.data.data;
        this.logisticsList = res.data.data.logistics;
        if(res.data.data.data.length!=0){
          this.sizeForm.province = res.data.data.data[0].zone_name;
        }else {
          this.sizeForm.province = '';
        }
        if(res.data.data.logistics.length!=0){
          this.sizeForm.shipping_code = res.data.data.logistics[0].id;
          this.logisticsChange(this.sizeForm.shipping_code);
          // this.priceTwo = 0;
        }else {
          this.sizeForm.shipping_code = '';
          this.priceTwo = 0;
        }
      }else {
        this.errorMess('获取城市列表失败')
      }
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    //悬浮商品图片并滚动
    wheel(imgValue,e=event) {
      this.bodyMouseWheel(e);
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
          // ;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    //滚动图片时->阻止页面滚动
    bodyMouseWheel(e){
      e.preventDefault();
    },
    mouseover(val){
      this.bigImgSrc = val[0].path;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
  },
  beforeMount(){
    this.initData(this.pageSize,this.currentPage);
    this.initCountryCity();
  },
  computed: {
    selfServiceOrderState(){
      return this.$store.state.selfServiceOrderState;
    },
    // sizeFormNum(){
    //   return this.sizeForm.goods_num;
    // }
  },
  watch: {
    selfServiceOrderState: function(){
      this.initData(this.pageSize,this.currentPage);
    },
    tableData:{
      handler: function (newVal) {
        if(this.sizeForm.province!=='') {
          this.logisticsChange(this.sizeForm.shipping_code);
        } 
      },
      deep: true    //深度监听
     },
     'sizeForm.province':function() {
       this.logisticsChange(this.sizeForm.shipping_code);
     },
  },
  components: {
  }
}
</script>


