/*自助下单页面*/
<template>
  <div class="mainCon selefService">
    <div class="mainTop">
      <div class="mainTopLeft">
        <div style='height:1.5vh;width: 100%; background: #f1f1f1;'></div>
        <div class="orderList" style="padding: 5px 10px;border-top-left-radius: 5px;border-top-right-radius: 5px;">备选商品列表</div>
        <div  class="orderList">
        <el-button type="success" size="mini" @click="handleClickAddShop"><i class="el-icon-plus "></i>添加</el-button>
        <el-button type="danger" size="mini" @click="handleClickDetailShop"><i class="el-icon-close "></i>删除</el-button>
        <el-button type="primary" size="mini" @click="handleClickOrder"><i class="iconfont icon_queshengyeicon_gouwuche "></i>下单</el-button>
        <el-button
          type="success"
          icon="el-icon-refresh"
          size="mini"
          @click="handleClickRefresh"
        >刷新</el-button>  
      </div>
      <div style="position:absolute; bottom: -5px!important;right: 20px;">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="pageSize"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total">
        </el-pagination>
        <div class="common">共{{ total }}笔</div>
        </div>
      </div>
    </div>
    <div class="mainBottom">
      <el-table
        :data="tableData"
        style="width: 97%;margin-left:10px;margin-right:15px;margin-top: 10px;min-height: 80.5vh;"
        @selection-change="handleSelectionChange"
        @row-dblclick="handleDBClick"
        :highlight-current-row="true"
        v-loading="loading"
        stripe>
       <el-table-column
          fixed
          type="index"
          min-width="50"
          style="z-index: 1!important">
        </el-table-column>
        <el-table-column
          fixed
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column
          prop="date"
          label="图片"
          min-width="80">
          <template slot-scope="scope">
            <span v-show="scope.row.goods_imgs.length>0">
              <el-tooltip placement="right-start">
                <div slot="content">
                  <img v-lazy="bigImgSrc" alt srcset class="bigImgCon">
                </div>
                <img v-lazy="scope.row.goods_imgs[0].path" alt srcset ref="user_image" @mouseover="mouseover(scope.row.goods_imgs)" @mousewheel="wheel(scope.row.goods_imgs)">
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          label="商品标题">
          <template slot-scope="scope">
            <p>
              {{ scope.row.goods_name }}
              <span>货号：{{ scope.row.goods_code }}</span>
            </p>
            <p>颜色：
              <span>{{ scope.row.goods_size.color }}</span>
            </p>
            <p>规格：
              <span>{{ scope.row.goods_size.size }}</span>
            </p>
          </template>
        </el-table-column>
        <el-table-column
          label="重量（KG）"
          min-width="100">
          <template slot-scope="scope">
            <p>{{ scope.row.goods_size.weight }}</p>
          </template>
        </el-table-column>
        <el-table-column
          label="单价"
          min-width="100">
          <template slot-scope="scope">
            <p>{{ scope.row.goods_size.msrp }}</p>
          </template>
        </el-table-column>
        <el-table-column
          label="状态"
          min-width="50">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="上架" placement="top">
              <i class="el-icon-check colorGreen" v-show="scope.row.alternative_status==2"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="未上架" placement="top">
              <i class="el-icon-close colorRed" v-show="!scope.row.alternative_status==2"></i>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="添加待选商品SKU" :visible.sync="dialogFormVisible">
        <el-form :model="form" :rules="formRules" ref="form" size="mini">
          <el-form-item label="" prop="textarea">
            <el-input type="textarea" rows="5" v-model="form.textarea" resize="none" autocomplete="off" placeholder="请以逗号分开"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="handleClose">取 消</el-button>
          <el-button size="small" type="primary" @click="handleSubmitSku('form')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  let imgIndex = 0;//bigImg索引
export default {
  data(){
    return {
      dialogFormVisible: false,//添加商品弹出框
      loading: false,
      currentPage: 1,//第几页
      pagerCount: 5,//按钮数量
      pageSize: 50,
      total: 0,
      form: {
        textarea: ''
      },
      formRules:{ textarea: [ { required: true, message: 'SKU为必选字段' } ] },
      // tableColumnState: false,//表格展开行显示状态
      rules: { name: [ { required: true, message: '标题不能为空' } ] },
      //初始化数据
      tableData: [],
      //初始化商品图片
      bigImgSrc: '',
      //多选数据集合
      multipleSelection: [],
      //处理数据源容器
      multipleSelectionId: [],
      activeName: 'first',//标签页默认显示
      ruleForm: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        switchValue: true
      },
    }
  },
   methods: {
     //关闭弹出框
    handleClose(){
      this.dialogFormVisible = false;
      this.form.textarea = '';
    },
    //添加sku
    handleSubmitSku(formName){
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let res = await this.api.goodsAlternativeAdd({
            sku: `${this.form.textarea}`
          })
          // this.postRequest(`/v1/goodsAlternativeAdd?sku=${this.form.textarea}`)
          // .then(res => {
          if(res.data.code=='200'){
            this.$message({
              type: 'success',
              message: '添加成功'
            })
          }
          if(res.data.code=='201'){
            this.$message.error('添加失败');
          }
          if(res.data.code=='202'){
            this.$message({
              type: 'warning',
              message: '该sku已存在于被选商品列表'
            })
          }
          this.dialogFormVisible = false;
          this.form.textarea = '';
          this.initData(this.currentPage, this.pageSize);
        } else {
          return false;
        }
      });
    },  
    handleSizeChange(val) {
      this.currentPage = val;
      this.initData(this.currentPage, this.pageSize);
    },
    handleCurrentChange(val) {
      this.pageSize = val;
      this.initData(this.currentPage, this.pageSize);
    },
    //点击添加商品函数
    handleClickAddShop(){
      // ;
      this.dialogFormVisible = true;
    },
    //删除备选商品
    async delPro(sku){
      let res = await this.api.goodsAlternativeDelete({sku: sku})
      // this.postRequest(`/v1/goodsAlternativeDelete?sku=${sku}`)
      // .then(res => {
      if(res.data.code=='200'){
        this.$message({
          type: 'success',
          message: '删除成功'
        })
        this.initData(this.currentPage, this.pageSize);
      }
    },
    //点击删除按钮
    handleClickDetailShop(){
      this.handleSKU(this.multipleSelection);
      let addShop = '';
      if(this.multipleSelectionId.length>0){
        addShop = this.multipleSelectionId.join(',');
        this.delPro(addShop);
      }else {
        this.$message({
          message: '请选择商品进行删除',
          type: 'warning'
        });
      }
    },
    //点击下单按钮
    handleClickOrder(){
      this.handleId(this.multipleSelection);
      let addShop = '';
      if(this.multipleSelectionId.length>0){
        addShop = this.multipleSelectionId.join(',');
        this.addClickOrder(addShop);
      }else {
        this.$message({
          message: '请选择商品进行下单',
          type: 'warning'
        });
      }
    },
    //下单
    async addClickOrder(addShop,u_id=sessionStorage.getItem('selectUId')){
      let res = await this.api.goodsAddCart({goods_id: addShop})
      // this.postRequest(`/v1/goodsAddCart?u_id=${u_id}&goods_id=${addShop}`)
      // .then((res)=>{
      this.$store.commit('CHANGE_SELF_SERVICE_STATE');
      if(res.data.data=='1'){
        this.$message({
          message: '添加成功',
          type: 'success'
        });
      }else {
        this.$message.error('添加失败');
      }
    },
    //某一行双击
    handleDBClick(val){
      this.addClickOrder(val.id);
    },
    //刷新页面
    handleClickRefresh(){
      this.initData(this.currentPage, this.pageSize);
    },
    //悬浮商品图片并滚动
    wheel(imgValue,e=event) {
      this.bodyMouseWheel(e);
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
          // ;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    mouseover(val){
      this.bigImgSrc = val[0].path;
    },
    //滚动图片时->阻止页面滚动
    bodyMouseWheel(e){
      e.preventDefault();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleId(data){
      this.multipleSelectionId = [];
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          const element = data[key];
          this.multipleSelectionId.push(element.id); 
        }
      }
    },
    handleSKU(data){
      this.multipleSelectionId = [];
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          const element = data[key];
          this.multipleSelectionId.push(element.sku); 
        }
      }
    },
    //初始化数据
    async initData(currentPage,pageSize,u_id=sessionStorage.getItem('selectUId')){
      this.loading = true;
      let res = await this.api.goodsAlternative({
        page: currentPage,
        limit: pageSize,
      })
      if(res.data.code == 200) {
        this.tableData = res.data.data.data;
        this.total = res.data.data.total;
        //结束加载状态
        //初始化bigImg
        this.bigImgSrc = res.data.data.data[0] ? res.data.data.data[0].goods_imgs[0].path : '';
        this.loading = false;
      }else {
        this.errorMess('获取备选商品列表失败')
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = [];
      for (const key in val) {
        if (val.hasOwnProperty(key)) {
          const element = val[key];
          this.multipleSelection.push(element); 
          for (const key in element.sub_goods) {
            if (element.sub_goods.hasOwnProperty(key)) {
              let id = element.sub_goods[key];
              this.multipleSelection.push(id); 
            }
          }
        }
      }
    },
  },
  beforeMount(){
    this.initData(this.currentPage, this.pageSize);
  },
  // watch: {
  //   currentPage: function(newVal,oldVal){
  //     this.initData(newVal, this.pageSize);
  //   },
  //   pageSize: function(newVal,oldVal){
  //     this.initData(this.currentPage, newVal);
  //   },
  // },
  components: {
  }
}
</script>


